@lost flexbox flex;

body {
	background-color: dodgerblue;
	lost-center: 1400px;
}

/*
	Playing with Lost Grid
*/

section {
	lost-row: 1/4;
}

.profile-pic {
	border-radius: 50%;
	max-width: 12rem;
	lost-column: 1/2;
}

.about-content {
	lost-column: 1/2;
}

/*
	Navigation
*/

.main-nav {
	display: flex;
	position: fixed;
	margin: auto;
	width: 100%;
	background: #f4f5f6;
	height: 4rem;
	z-index: 99;
}

.main-nav li {
	align-self: center;
	margin-left: 8px;
	margin-right: 8px;
	list-style: none;
}

.main-logo:first-child {
	/*margin-right: auto;*/
	/*flex-grow: 1;*/
}

.main-logo h1 {
	font-size: 2.5rem;
}

.main-logo a,
.main-nav a {
	display: block;
	text-decoration: none;
	text-align: center;
	padding: 8px 15px;
	position: relative;
	overflow: hidden;
}